import React, { useEffect } from 'react';
import PropTypes from 'prop-types';
import { useSelector, useDispatch } from 'react-redux';

// Components
import FormStyled from '../components.styled/Form.styled';
import FormComposer from './FormComposer';
import Button from '../../button';

// theme
import { ThemeProvider } from '../../theme/themeContext';
import { defaultTheme } from '../../theme/form';

// Selectors
import {
  makeSelectFormStatus,
  makeSelectPagedFields,
  makeSelectPagingInfo,
  makeSelectDefaultLang,
  makeSelectFormSettings,
  makeSelectFormEntries,
  makeSelectFormPostData,
  makeSelectFormFieldErrors
} from '../../redux/selectors';

// Actions
import {
  setCheckboxValue,
  setDateRangeValues,
  setFormId,
  setValue,
  submitForm,
  togglePageBack,
  togglePageForward,
  validateField
} from '../../redux/actions';

const Form = ({
  className,
  formId,
  useDefaultTheme = true,
  customSubmit,
}) => {

  const dispatch = useDispatch();
  const _setFormId = (formId) => {
    dispatch(setFormId(formId));
  }
  const _setValue = (formId, id, value) => {
    dispatch(setValue(formId, id, value));
  }
  const _setCheckboxValue = (formId, id, value, isChecked) => {
    dispatch(setCheckboxValue(formId, id, value, isChecked));
  }
  const _setDateRangeValues = (formId, id, dateType, value) => {
    dispatch(setDateRangeValues(formId, id, dateType, value));
  }
  const _validateField = (formId, id, value) => {
    dispatch(validateField(formId, id, value));
  }
  const _togglePageForward = (formId, pageIndex) => {
    dispatch(togglePageForward(formId, pageIndex));
  }
  const _togglePageBack = (formId, pageIndex) => {
    dispatch(togglePageBack(formId, pageIndex)); 
  }
  const _submitForm = formId => {
    dispatch(submitForm(formId));
  }

  useEffect(() => {
    if (formId) _setFormId(formId)
  }, [formId]); 


  const selectFormFields = makeSelectPagedFields(formId);
  const selectFormStatus = makeSelectFormStatus(formId);
  const selectPagingInfo = makeSelectPagingInfo(formId);
  const selectDefaultLang = makeSelectDefaultLang(formId);
  const selectFormSettings = makeSelectFormSettings(formId);
  const selectFormFieldErrors = makeSelectFormFieldErrors(formId)
  const selectFormEntries = makeSelectFormEntries(formId);
  const selectFormPostData = makeSelectFormPostData(formId);

  const status = useSelector(selectFormStatus);
  const fields = useSelector(selectFormFields);
  const pagingInfo = useSelector(selectPagingInfo);
  const defaultLanguage = useSelector(selectDefaultLang);
  const settings = useSelector(selectFormSettings);
  const errors = useSelector(selectFormFieldErrors);
  const entries = useSelector(selectFormEntries);
  const formData = useSelector(selectFormPostData);

  if (pagingInfo && pagingInfo.pageCount > 1) {
    const isLastPage = pagingInfo.pageCount == pagingInfo.pageIndex + 1;
    return (
      <ThemeProvider theme={defaultTheme}>
      <FormStyled
        className={className}
        id={formId}
        useDefaultTheme={useDefaultTheme}
      >
        {!status ||
          (status &&
            !status.isLoading &&
            !status.isSubmitting &&
            !status.hasSuccess && (
              <>
                {pagingInfo.pageIndex > 0 && (
                  <Button
                    type="button"
                    text="Go Back"
                    action={() =>
                      _togglePageBack(formId, pagingInfo.pageIndex - 1)
                    }
                    useDefaultTheme={useDefaultTheme}
                  />
                )}
                <FormComposer
                  fields={fields}
                  formData={formData}
                  formId={formId}
                  setValue={_setValue}
                  validateField={_validateField}
                  defaultLanguage={defaultLanguage}
                  errors={errors}
                  pagingInfo={pagingInfo}
                  useDefaultTheme={useDefaultTheme}
                  entries={entries}
                  setDateRangeValues={_setDateRangeValues}
                  setCheckboxValue={_setCheckboxValue}
                />
                {!isLastPage && (
                  <Button
                    type="button"
                    text="Next"
                    action={() =>
                      _togglePageForward(formId, pagingInfo.pageIndex + 1)
                    }
                    useDefaultTheme={useDefaultTheme}
                  />
                )}
                {isLastPage && (
                  <Button
                    text={settings?.submitButtonText || "Submit"}
                    type="button"
                    action={() => {
                      _submitForm(formId);
                      if (customSubmit) customSubmit();
                    }}
                    useDefaultTheme={useDefaultTheme}
                  />
                )}
              </>
            ))}
        {status && status.hasSuccess && status.successMessage && (
          <p className="success-message">{status.successMessage}</p>
        )}
        </FormStyled>
        </ThemeProvider>
    )
  } else {
    return (
      <ThemeProvider theme={defaultTheme}>
      <FormStyled
        className={className}
        id={formId}
        useDefaultTheme={useDefaultTheme}
      >
        {!status ||
          (status &&
            !status.isLoading &&
            !status.isSubmitting &&
            !status.hasSuccess && (
              <>
                <FormComposer
                  fields={fields}
                  formId={formId}
                  setValue={_setValue}
                  setDateRangeValues={_setDateRangeValues}
                  validateField={_validateField}
                  defaultLanguage={defaultLanguage}
                  errors={errors}
                  useDefaultTheme={useDefaultTheme}
                  entries={entries}
                  setCheckboxValue={_setCheckboxValue}
                />
                <Button
                  text={settings?.submitButtonText || "Submit"}
                  type="button"
                  action={() => {
                    _submitForm(formId);
                    if (customSubmit) customSubmit();
                  }}
                  useDefaultTheme={useDefaultTheme}
                />
              </>
            ))}
        {status && status.hasSuccess && status.successMessage && (
          <p className="success-message">{status.successMessage}</p>
        )}
        </FormStyled>
        </ThemeProvider>
    );
  }
};

Form.propTypes = {
  className: PropTypes.string,
  formId: PropTypes.string,
  useDefaultTheme: PropTypes.bool,
  customSubmit: PropTypes.func,
};

export default Form;
