import { createSelector } from 'reselect';

export const selectForms = state => state.forms;

export const makeSelectPagedFields = formId =>
  createSelector([selectForms], form => {
    if (form[formId]) {
      const pagingInfo = form[formId].pagingInfo;
      const fields = form[formId].fields;
      if (fields?.length > 0 && pagingInfo.pageCount > 1) return fields.filter(f => f.groupId == pagingInfo.currentPageId);
      else return fields;
    }
  });

export const makeSelectFormStatus = formId =>
  createSelector(selectForms, form => form[formId]?.status);
    
export const makeSelectPagingInfo = formId =>
  createSelector(selectForms, form => form[formId]?.pagingInfo);

export const makeSelectFormSettings = formId =>
  createSelector(selectForms, form => form[formId]?.settings)
    
export const makeSelectFormFieldErrors = formId =>
  createSelector(selectForms, form => form[formId]?.fieldErrors);

export const makeSelectFormEntries = formId =>
  createSelector(selectForms, form => form[formId]?.entries);

export const makeSelectFormPostData = formId =>
  createSelector(selectForms, form => form[formId]?.data);

export const makeSelectIsLoading = formId =>
  createSelector(selectForms, forms => forms[formId]?.status.isLoading);

export const makeSelectHasSuccess = formId =>
  createSelector(selectForms, forms => forms[formId]?.status.hasSuccess);

export const makeSelectFormFields = formId =>
  createSelector(selectForms, forms => forms[formId]?.fields);

export const makeSelectDefaultLang = formId =>
  createSelector(selectForms, forms => forms[formId]?.defaultLanguage);

export const makeSelectFormSuccessMessage = formId =>
  createSelector(selectForms, forms => forms[formId]?.status.successMessage);

export const makeSelectFormGroup = formId =>
  createSelector(selectForms, forms => forms[formId]?.groups);


export const selectors = {
  selectForms,
  makeSelectPagedFields,
  makeSelectFormStatus,
  makeSelectPagingInfo,
  makeSelectFormSettings,
  makeSelectFormFieldErrors,
  makeSelectFormEntries,
  makeSelectFormPostData,
  makeSelectIsLoading,
  makeSelectHasSuccess,
  makeSelectFormFields,
  makeSelectDefaultLang,
  makeSelectFormSuccessMessage,
  makeSelectFormGroup
}
